import React, { Component } from 'react';

class Info extends Component {
    render() {
      const headerStyle = {
        backgroundColor: 'white', 
        color: 'white'

      }
        return (
          <div class="container-fluid">
          <div class="row mx-auto style=">
                  <div class="col-md-8 col-sm-8 offset-sm-2 offset-md-2 pt-3"><div class="card">
                      <div class="card-header text-center pb-2" style={{headerStyle}}>
                       <h3>Welcome to My Personal Page!</h3>
                      </div>
                      <div class="card-body text-center">
                        <h5 class="card-title">You can find out things about me on this website. Thank you for visiting.</h5>
                        <p class="card-text">Hello! My name is Ömer Onur.
                        I am 22 years old student who trying to learn JavaScript.
                        I am studying at Aksaray University in Aksaray, Turkey. 
                        My department is Electrical and Electronics Engineering, 
                          so I have to learn new things and catch the technology.
                        This is the reason of the my interest of JavaScript. 
                        I think, we all have to catch the technology because it is improving at the speed of light. 
                        Therefore I am full-focused for learning new things and using actively.
                        I think, I am on the right way. 
                        You can see my biography and more about me at the 'About Me' page.
                        </p>
                        <a href="https://omeronuralpaslan.github.io/about.html" class="btn btn-success">
                        <small class="text-center">Click here for more.</small></a>
                      </div>
                    </div></div>
              </div>
              </div>
        );
    }
}

export default Info;